body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif; }

.main-page-section .input-form {
  padding: 1rem;
  display: flex;
  justify-content: center; }
  .main-page-section .input-form .input-field {
    border: none;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    border-radius: 10px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.199); }

.main-page-section .image-list {
  padding: 1rem;
  columns: 4; }
  .main-page-section .image-list a {
    display: inline-block;
    list-style: none; }
    .main-page-section .image-list a img {
      border-radius: 10px;
      transition: 0.3s;
      width: 100%; }
    .main-page-section .image-list a img:hover {
      opacity: 0.5; }

.image-page .header {
  z-index: 3;
  position: fixed;
  padding: 1rem 0rem 0rem 2.5rem; }
  .image-page .header .back-button {
    border: none;
    border-radius: 20px;
    padding: 1rem;
    background-color: white;
    font-weight: 900;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.199); }

.image-page .image-preview-section {
  margin: 1rem auto;
  padding-right: 1rem;
  display: flex;
  width: 50%;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.199); }
  .image-page .image-preview-section img {
    width: 50%; }
  .image-page .image-preview-section .photo-details {
    padding-left: 1rem;
    text-align: center;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .image-page .image-preview-section .photo-details .photo-description {
      font-size: 2rem; }
    .image-page .image-preview-section .photo-details .review-section {
      display: flex;
      justify-content: center;
      align-items: center; }
      .image-page .image-preview-section .photo-details .review-section p {
        margin: 1rem; }
    .image-page .image-preview-section .photo-details .author-details {
      display: flex;
      align-items: center; }
      .image-page .image-preview-section .photo-details .author-details .author-profile-name span {
        font-weight: 900; }
      .image-page .image-preview-section .photo-details .author-details .author-profile-photo {
        margin: 1rem;
        width: 50px;
        height: 50px;
        border-radius: 100%; }

.image-page .related-images-section {
  padding: 1rem; }
  .image-page .related-images-section section {
    padding: 1rem;
    columns: 4; }
    .image-page .related-images-section section a {
      display: inline-block;
      list-style: none; }
      .image-page .related-images-section section a img {
        border-radius: 10px;
        transition: 0.3s;
        width: 100%; }
      .image-page .related-images-section section a img:hover {
        opacity: 0.5; }

@media screen and (max-width: 800px) {
  .image-page .image-preview-section {
    width: 80%; }
  .image-page .related-images-section {
    border: tomato 5px solid; }
    .image-page .related-images-section section {
      columns: 3; } }

@media screen and (max-width: 600px) {
  .image-page .image-preview-section {
    padding: 0rem 0rem 1rem;
    width: 90%;
    flex-direction: column; }
    .image-page .image-preview-section img {
      width: 100%; }
    .image-page .image-preview-section .photo-details {
      width: 100%;
      padding-left: 0rem; }
  .image-page .related-images-section {
    border: teal 5px solid; }
    .image-page .related-images-section section {
      padding: 0;
      columns: 2; } }
